import * as userService from "../service/users";
import ModalUser from "../components/ModalUser.vue";
export default {
  components: {
    ModalUser
  },
  data() {
    return {
      userData: [],
      modalData: {},
      modalMode: ""
    };
  },
  methods: {
    async fetchUser() {
      debugger;
      let data = await userService.fetchUser();
      this.userData = data || [];
    },
    showModal(data) {
      this.modalData = data;
      this.modalMode = "edit";
      $("#modalUser").modal("show");
    },
    add() {
      this.modalMode = "add";
      this.modalData = {};
      $("#modalUser").modal("show");
    }
  },
  mounted() {
    this.fetchUser();
  }
};
